// _typography.scss

h1 {
    font-weight: initial;
    @include gotham-book();
    font-size: 4rem;
    line-height: 1.2;
    letter-spacing: -1.27px;
    margin: 0;
}

h2 {
    font-weight: initial;
    @include gotham-medium();
    font-size: 4rem;
    line-height: 1.2;
    letter-spacing: -1.27px;
    margin: 0;
}

h3 {
    font-weight: initial;
    @include gotham-medium();
    font-size: 3rem;
    line-height: 1.5667;
    letter-spacing: -1px;
    margin: 0;
}

h4 {
    font-weight: initial;
    @include gotham-book();
    font-size: 3rem;
    line-height: 1.333;
    letter-spacing: -0.3px;
    margin: 0;
}

h5 {
    font-weight: initial;
    @include gotham-medium();
    font-size: 2.5rem;
    line-height: 1.2;
    letter-spacing: -0.8px;
    margin: 0;
}

h6 {
    font-weight: initial;
    @include gotham-bold();
    font-size: 1.7rem;
    line-height: 1;
    letter-spacing: -0.24px;
    margin: 0;
}

p {
    font-weight: initial;
    @include gotham-book;
    font-size: 1.8rem;
    line-height: 1.222;
    letter-spacing: -0.57px;
}

strong {
    font-weight: initial;
}

em {
    font-weight: initial;
}

small {
    font-size: 1.3rem;
    letter-spacing: -0.67px;
    @include gotham-book();
}

a, a:hover, a:active, a:visited, a:link {
    text-decoration: none;

    &:hover {

    }
}
